.flickity-page-dots {
  position: fixed;
  display: flex;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10; }

.dot {
  position: relative;
  width: 11px;
  height: 11px;
  margin: 0 3px;
  padding: 0;
  border: none;
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.1);
  text-indent: -9999px;
  outline: none; }
  .dot:hover {
    background: white;
    border-color: white;
    cursor: pointer; }

.dot.is-selected {
  width: 15px;
  height: 15px;
  margin-top: -2px;
  background: white;
  border-color: white;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.5); }
  .dot.is-selected:hover {
    cursor: normal; }

@media only screen and (min-width: 0) and (max-width: 450px) {
  .flickity-page-dots {
    align-items: center;
    justify-content: center;
    bottom: 0;
    width: 100%;
    padding: 10px 0;
    background: rgba(0, 0, 0, 0.1); } }

@media only screen and (min-height: 320px) and (max-height: 480px) {
  .flickity-page-dots {
    align-items: center;
    justify-content: center;
    bottom: 0;
    width: 100%;
    padding: 10px 0;
    background: rgba(0, 0, 0, 0.1); } }

